@font-face {
  font-family: 'Trebuchet MS';
  font-weight: normal;
  font-style: normal;
  src: url("../fonts/TrebuchetMS.woff") format("woff");
  font-display: swap; }

@font-face {
  font-family: 'Exo 2';
  font-weight: thin;
  font-style: normal;
  src: url("../fonts/exo2thin.woff2") format("woff2");
  font-display: swap; }

.main-content {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  background-color: #252627; }

@-ms-viewport {
  width: device-width; }

.page {
  box-sizing: border-box;
  min-width: 320px;
  min-height: 100%;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
  -ms-overflow-style: scrollbar;
  -ms-text-size-adjust: 100%; }
  .page__inner {
    display: flex;
    flex-direction: column;
    min-height: 100vh; }
  .page__content {
    flex-grow: 1; }
  .page__footer-wrapper {
    flex-shrink: 0; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  margin: 0;
  min-height: 100%;
  background-color: #fff; }

[tabindex='-1']:focus {
  outline: none !important; }

[role='button'] {
  cursor: pointer; }

input[type='search'] {
  -webkit-appearance: textfield; }

a,
button,
[role='button'],
input,
label,
select,
textarea {
  touch-action: manipulation; }

a svg,
button svg {
  pointer-events: none; }

.container {
  display: flex;
  align-items: center;
  flex-direction: column;
  margin: 0 auto;
  max-width: 1240px;
  padding-right: 0.625rem;
  padding-left: 0.625rem;
  width: 100%; }

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0; }

body {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  margin: 0 auto;
  min-width: 100%;
  min-height: 100%;
  color: #000;
  font-size: 16px; }

.main-img {
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  min-width: 100%;
  min-height: 100%; }

.js-inactive {
  display: none; }

.js-active {
  display: block; }

.cat-food {
  display: flex;
  position: relative;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  align-items: center;
  flex-direction: row;
  z-index: 100;
  width: 100%; }
  .cat-food__main-title {
    margin-top: 0;
    margin-bottom: 1.5rem;
    text-align: center;
    text-shadow: 0 1px 0 #000000;
    color: #ffffff;
    font-family: 'Exo 2', sans-serif;
    font-size: 2.25rem;
    font-weight: 100; }

@media (max-width: 1240px) {
  .cat-food {
    padding-top: 3.125rem; } }

@media (min-width: 1240px) {
  .cat-food {
    position: absolute; } }

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  margin-right: 0;
  margin-left: 0;
  width: 100%;
  overflow: hidden; }

@media (max-width: 1240px) {
  .cards {
    justify-content: center; } }

@media screen and (max-width: 1240px) and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .cards {
    height: 1600px; } }

.food-card {
  position: relative;
  flex: 1;
  margin-right: 2.5rem;
  margin-left: 2.5rem; }
  .food-card__wrapper {
    position: relative;
    margin: 0 auto;
    margin-bottom: 0.9375rem;
    width: 20rem;
    height: 30rem;
    outline: none;
    border-radius: 0.625rem;
    cursor: pointer; }
    .food-card__wrapper--default {
      background: linear-gradient(135deg, transparent 1.875rem, #1698d9 1.875rem); }
    .food-card__wrapper--selected {
      background: linear-gradient(135deg, transparent 1.875rem, #d91667 1.875rem); }
    .food-card__wrapper--selected-hover {
      background: linear-gradient(135deg, transparent 1.875rem, #e62e7a 1.875rem); }
    .food-card__wrapper--disabled {
      background: linear-gradient(135deg, transparent 1.875rem, #b3b3b3 1.875rem);
      cursor: not-allowed; }
    .food-card__wrapper--hover {
      background: linear-gradient(135deg, transparent 1.875rem, #2ea8e6 1.875rem); }
  .food-card__content {
    box-sizing: border-box;
    position: absolute;
    top: 0.25rem;
    left: 0.25rem;
    width: 19.5rem;
    height: 29.5rem;
    border-radius: 0.625rem;
    background: linear-gradient(135deg, transparent 29px, #f2f2f2 29px);
    padding-top: 1.125rem;
    padding-left: 2.75rem;
    overflow: hidden; }
  .food-card__img {
    position: absolute;
    bottom: -0.375rem;
    left: -0.1875rem; }
  .food-card__promo {
    letter-spacing: 0.01px;
    color: #666666;
    font-family: 'Trebuchet MS', sans-serif;
    font-size: 1rem; }
  .food-card__promo-cat {
    display: none;
    color: #e62e7a; }
  .food-card__title {
    margin-top: 0.3125rem;
    margin-bottom: 0.1875rem;
    letter-spacing: 1.3px;
    font-family: 'Trebuchet MS', sans-serif;
    font-size: 3rem; }
  .food-card__stuffing {
    margin-bottom: 1.125rem;
    line-height: 0.9;
    letter-spacing: 1px;
    font-family: 'Trebuchet MS', sans-serif;
    font-size: 1.5rem;
    font-weight: 700; }
  .food-card__description-item {
    color: #666666;
    font-family: 'Trebuchet MS', sans-serif;
    font-size: 0.875rem; }
  .food-card__description-number {
    font-weight: 700; }
  .food-card__weight {
    box-sizing: border-box;
    display: inline-block;
    position: absolute;
    right: 0.625rem;
    bottom: 0.75rem;
    min-width: 5rem;
    min-height: 5rem;
    border-radius: 5rem;
    padding-top: 1.25rem;
    padding-bottom: 0.625rem;
    text-align: center;
    line-height: 0.9;
    color: #fff;
    font-family: 'Trebuchet MS', sans-serif; }
  .food-card__weight--default {
    background-color: #1698d9; }
  .food-card__weight--default-hover {
    background-color: #2ea8e6; }
  .food-card__weight--selected {
    background-color: #d91667; }
  .food-card__weight--selected-hover {
    background-color: #e62e7a; }
  .food-card__weight--disabled {
    background-color: #b3b3b3; }
  .food-card__weight-value {
    line-height: 0.6;
    font-size: 2.625rem; }
  .food-card__weight-units {
    font-size: 1.25rem; }
  .food-card__disabled-layout--on {
    position: absolute;
    bottom: 0;
    left: 0;
    opacity: 0.5;
    z-index: 100;
    width: 100%;
    height: 100%;
    background: linear-gradient(135deg, transparent 29px, #f2f2f2 29px); }
  .food-card__item--disabled {
    background-color: #f6f6f6; }
  .food-card__text {
    margin-top: 0.75rem;
    text-align: center;
    text-shadow: 0 0.063rem 0 #000000;
    letter-spacing: -0.5px;
    color: #fff;
    font-family: 'Trebuchet MS', sans-serif;
    font-size: 0.875rem; }
  .food-card__text--out-of-stock {
    color: #ffff66; }
  .food-card__text-link {
    text-decoration-style: dashed;
    color: #1698d9; }
    .food-card__text-link:hover {
      color: #2ea8e6; }
    .food-card__text-link:visited {
      color: #2ea8e6; }
    .food-card__text-link:active {
      color: #ffff66; }
    .food-card__text-link:focus {
      color: #ffff66; }

@media (max-width: 1240px) {
  .food-card {
    margin-bottom: 2.5rem; } }

@media screen and (max-width: 1240px) and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .food-card {
    margin-right: 0;
    margin-left: 0;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center; } }
