// В этом файле должны быть стили для БЭМ-блока food-card, его элементов, 
// модификаторов, псевдоселекторов, псевдоэлементов, @media-условий...

.food-card {
  position: relative;
  flex: 1;
  margin-right: 2.5rem;
  margin-left: 2.5rem;

  &__wrapper {
    position: relative;
    margin: 0 auto;
    margin-bottom: 0.9375rem;
    width: 20rem;
    height: 30rem;
    outline: none;
    border-radius: 0.625rem;
    cursor: pointer;

    &--default {
      background: linear-gradient(135deg, transparent 1.875rem, $color-default 1.875rem);
    }

    &--selected {
      background: linear-gradient(135deg, transparent 1.875rem, $color-active 1.875rem);
    }

    &--selected-hover {
      background: linear-gradient(135deg, transparent 1.875rem, $color-active-hover 1.875rem);
    }

    &--disabled {
      background: linear-gradient(135deg, transparent 1.875rem, $color-disabled 1.875rem);
      cursor: not-allowed;
    }

    &--hover {
      background: linear-gradient(135deg, transparent 1.875rem, $color-default--hover 1.875rem);
    }
  }


  &__content {
    box-sizing: border-box;
    position: absolute;
    top: 0.25rem;
    left: 0.25rem;
    width: 19.5rem;
    height: 29.5rem;
    border-radius: 0.625rem;
    background: linear-gradient(135deg, transparent 29px, $color-card-bg 29px);
    padding-top: 1.125rem;
    padding-left: 2.75rem;
    overflow: hidden;
  }

  &__img {
    position: absolute;
    bottom: -0.375rem;
    left: -0.1875rem;
  }

  &__promo {
    letter-spacing: 0.01px;
    color: $description-text-color;
    font-family: 'Trebuchet MS', sans-serif;
    font-size: 1rem;
  }

  &__promo-cat {
    display: none;
    color: $color-active-hover;
  }

  &__title {
    margin-top: 0.3125rem;
    margin-bottom: 0.1875rem;
    letter-spacing: 1.3px;
    font-family: 'Trebuchet MS', sans-serif;
    font-size: 3rem;
  }

  &__stuffing {
    margin-bottom: 1.125rem;
    line-height: 0.9;
    letter-spacing: 1px;
    font-family: 'Trebuchet MS', sans-serif;
    font-size: 1.5rem;
    font-weight: 700;
  }

  &__description-item {
    color: $description-text-color;
    font-family: 'Trebuchet MS', sans-serif;
    font-size: 0.875rem;
  }

  &__description-number {
    font-weight: 700;
  }

  &__weight {
    box-sizing: border-box;
    display: inline-block;
    position: absolute;
    right: 0.625rem;
    bottom: 0.75rem;
    min-width: 5rem;
    min-height: 5rem;
    border-radius: 5rem;
    padding-top: 1.25rem;
    padding-bottom: 0.625rem;
    text-align: center;
    line-height: 0.9;
    color: #fff;
    font-family: 'Trebuchet MS', sans-serif;
  }

  &__weight--default {
    background-color: $color-default;
  }

  &__weight--default-hover {
    background-color: $color-default--hover;
  }

  &__weight--selected {
    background-color: $color-active;
  }

  &__weight--selected-hover {
    background-color: $color-active-hover;
  }

  &__weight--disabled {
    background-color: $color-disabled;
  }

  &__weight-value {
    line-height: 0.6;
    font-size: 2.625rem;
  }

  &__weight-units {
    font-size: 1.25rem;
  }

  &__disabled-layout--on {
    position: absolute;
    bottom: 0;
    left: 0;
    opacity: 0.5;
    z-index: 100;
    width: 100%;
    height: 100%;
    background: linear-gradient(135deg, transparent 29px, $color-card-bg 29px);
  }

  &__item--disabled {
    background-color: #f6f6f6;
  }

  &__text {
    margin-top: 0.75rem;
    text-align: center;
    text-shadow: 0 0.063rem 0 #000000;
    letter-spacing: -0.5px;
    color: #fff;
    font-family: 'Trebuchet MS', sans-serif;
    font-size: 0.875rem;
  }

  &__text--out-of-stock {
    color: $color-hghlight;
  }

  &__text-link {
    text-decoration-style: dashed;
    color: $color-default;

    &:hover {
      color: $color-default--hover;
    }

    &:visited {
      color: $color-default--hover;
    }

    &:active {
      color: $color-hghlight;
    }

    &:focus {
      color: $color-hghlight;
    }
  }
}

@media (max-width: $screen-lg) {
  .food-card {
    margin-bottom: 2.5rem;
  }
}

@media screen and (max-width: $screen-lg ) and (-ms-high-contrast: none), (-ms-high-contrast: active) {
  .food-card {
    margin-right: 0;
    margin-left: 0;
    width: 100%;
    flex-direction: column;
    justify-content: center;
    align-items: center
  }
}